.page-content {
    margin-top: 45px;
    margin-left: 24px;
    display: grid;
    grid-template-columns: repeat(5, 150px);
    grid-template-rows: repeat(3, 120px);
    grid-column-gap: 20px;
    grid-row-gap: 65px;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.grid-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.icon {
    margin: 12px 22px 9px 23px;
    padding-top: 12px;
    padding-bottom: 12px;
}

.icon-blue {
    margin: 12px 22px 9px 23px;
    padding-right: 12px;
    padding-left: 12px;
    padding-top: 12px;
    background: #E6F5FF;
