}
.searchbar {
    border: 1px solid #DDE0E4;
    border-radius: 8px;
    padding-left: 12px;
    padding-right: 12px;
    position: absolute;
    margin-left: 772px;
    margin-top: 2px;
    margin-top: -30px;
}

#searchIcon {
    font-size: 13px;
    color: #AFB2B6;
    position: relative;
    opacity: 0.6;
}

.searchbar-input{
    font-size:14px;
    height: 32px;
    width: 204px;
    padding-left: 8px;
    border: none;
